@import url(https://fonts.googleapis.com/css?family=Pacifico&display=swap);
@import url(https://fonts.googleapis.com/css?family=Source+Code+Pro&display=swap);
#bg-video{
  width: 100%;
  height: 100%;
  -o-filter: blur(3px);
  -webkit-filter: blur(3px);
          filter: blur(3px);
  object-fit: cover;
  -webkit-transform: scale(1.00);
          transform: scale(1.00);
  right: 0;
  bottom: 0;
  min-width: 100%;
  position: fixed;
}

.footer{
  height: 40px;
  font-size: 20px;
  text-align: right;
  font-family: 'Source Code Pro', monospace;
  color: var(--text-blue);
  background-color: var(--bg-pink);
  padding-right: 10px;
}

.footer-kickback{
  font-weight: bolder;
}

@media only screen and (max-width: 768px) {
  .footer{
    position: relative;
    display: block;
    background-color: var(--bg-blue);
    color: var(--text-pink);
    font-size: 15px;
    bottom: 0px;
  }

}
.clothing_container{
  background-color: var(--bg-blue);
  width: 100vw;
  text-align: center;
}

.clothing_title{
  color: #f1f1f1;
  font-family: 'Source Code Pro', monospace;
  font-size: 120px;
  font-weight: 800;
}

.title_inner{
  position: absolute;
  width: 100vw;
  bottom: 50vh;
  background-color: rgba(0, 0, 0, 0.0);
}

#scroll-button{
  position: absolute;
  width: 100%;
  bottom: 10vh;
  cursor: pointer;
  font-size: 80px;
}

.clothing_img_container{
  position: absolute;
  top: 100vh;
  width: 100%;
}

.clothing-img{
  width: 40vw;
  height: auto;
  padding: 10px;
}

.by-sam{
  bottom: 0;
}

@media only screen and (max-width: 768px) {
  .title_inner, .by-sam{
    width: 100%;
    font-size: 70px;
  }

  .clothing-footer{
  }

}
.bm-burger-button {
  position: fixed;
  width: 36px;
  height: 20px;
  left: 50px;
  top: 40px;
}
.bm-burger-bars {
  background: rgba(255, 255, 255, 1);
}
.bm-burger-bars-hover {
  background: #e5c0A9;
}
.bm-cross-button {
  height: 24px;
  width: 24px;
}
.bm-cross {
  background:var(--text-pink);
}
.bm-menu-wrap {
  position: fixed;
  height: 100%;
}
.bm-menu {
  background-color: var(--bg-blue);
  opacity: 0.99;
  padding: 1em 1.5em 0;
  font-size: 1.15em;
}
.bm-morph-shape {
  fill: #373a47;
}
.bm-item-list {
  color: var(--text-pink);
  padding: 0.8em;
}
.bm-item {
  display: inline-block;
  color: var(--text-pink);
  text-align: center;
  font-size: 30px;
  font-family: 'Pacifico', cursive;
}
.bm-overlay {
  background: black;
  height: 100%;
  width: 100%;
}
*:focus {
  outline: none;
}
.show-on-full{
  visibility: visible;
}
.no-show-on-full{
  visibility: hidden;
}

@media only screen and (max-width: 768px) {
  .mobile-menu-button{
    visibility: visible;
  }
  .bm-burger-button {
    visibility: visible;
    left: 10px;
  }
  .no-show-on-full{
    visibility: visible;
  }
}
.home-grid-wrapper{
  display: grid;
  grid-template-columns: 3fr 2fr;
  background-color: var(--bg-pink);
}

.title-wrapper{
  font-family: 'Source Code Pro', monospace;
  background-color: var(--bg-pink);
  color: var(--text-blue);
  height: calc(1vh * 95);
  height: calc(var(--vh, 1vh) * 95);
  font-size: 35vh;
  text-decoration: underline;
  padding-left: 20px;
}

.sections-wrapper{
  font-family: 'Source Code Pro', monospace;
  background-color: var(--bg-blue);
  color: var(--text-pink);
  height: 95vh;
  text-align: right;
  font-size: 15vh;
}

.section-button{
  border-bottom: 2px solid var(--text-blue);
}

.section-button:hover{
  color: yellow;
  -webkit-transition: ease-in .25s;
  transition: ease-in .25s;
  background-color: var(--bg-pink);
  cursor: pointer;
}

.location{
  position: relative;
  color: var(--text-blue);
  font-size: 3vh;
  margin-top: 32vh;
  margin-right: 1vh;
}

.rotate {
  -webkit-transform-origin: bottom right;
          transform-origin: bottom right;
  transform: rotate(80deg);
  -webkit-transform: rotate(90deg);
  -moz-transform: rotate(90deg)

}

.mobile-clock{
  display: none;
}

@media only screen and (max-width: 768px) {

  .home-grid-wrapper{
    display: grid;
    grid-template-columns: 1fr;
  }

  .title-wrapper{
    font-size: 110px;
    height: 80vh;
    padding-top: 25vh;
    padding-left: 0;
    text-align: center;
    text-decoration: none;
  }

  .sections-wrapper{
    font-size: 10vw;
    text-align: left;
    display: none;
  }

  .location{
    display: none;
  }

  .mobile-clock{
    display: block;
    font-size: 20px;
    color: black;
  }
}
.sam-image-container{
  position: relative;
  bottom: 0;
}

.sam-bio{
  position: absolute;
  font-family: 'Source Code Pro', monospace;
  font-size: 60px;
  background-color: rgba(35, 25, 66, 0.9);
  width: 55vw;
  right: 0px;
  top: 0px;
  z-index: 1000;
  color: white;
  height: 160vh;
  overflow-y: hidden;
  text-align: right;
  padding-right: 5px;
}

@media only screen and (max-width: 768px) {
  
  .sam-image-container{
    display: none;
  }
}
:root {
  --bg-blue: #004857;
  --bg-pink: #810085;
  --text-pink: #F974FF;
  --text-blue: #C2F5FF;
}
html{
  background-color: #004857;
  background-color: var(--bg-blue);
  overflow-x: hidden;
}
::selection {
  background: #ffb7b7; /* WebKit/Blink Browsers */
}

::-moz-selection {
  background: #ffb7b7; /* Gecko Browsers */
}

a:visited {
  color: inherit;
}

a{
  text-decoration: none;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

