.clothing_container{
  background-color: var(--bg-blue);
  width: 100vw;
  text-align: center;
}

.clothing_title{
  color: #f1f1f1;
  font-family: 'Source Code Pro', monospace;
  font-size: 120px;
  font-weight: 800;
}

.title_inner{
  position: absolute;
  width: 100vw;
  bottom: 50vh;
  background-color: rgba(0, 0, 0, 0.0);
}

#scroll-button{
  position: absolute;
  width: 100%;
  bottom: 10vh;
  cursor: pointer;
  font-size: 80px;
}

.clothing_img_container{
  position: absolute;
  top: 100vh;
  width: 100%;
}

.clothing-img{
  width: 40vw;
  height: auto;
  padding: 10px;
}

.by-sam{
  bottom: 0;
}

@media only screen and (max-width: 768px) {
  .title_inner, .by-sam{
    width: 100%;
    font-size: 70px;
  }

  .clothing-footer{
  }

}